export default {
	data() {
		return {
			godate: '',
			dateweek: '',
			tailName: '',
			startTrainState: '',
			startTrainName: '',
			departureTime: '',
			timeLength: '',
			endTrainState: '',
			endTrainName: '',
			arrivalTime: '',
		}
	},
	methods: {},
	onLoad: function(e) {
		this.godate = e.date;
		this.dateweek = e.week;
		console.log(e.newsid);
		uni.request({
			url: 'http://39.105.50.203/search/order.json',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				console.log(res.data.tailName)
				this.tailName = res.data.tailName;
				this.startTrainState = res.data.startTrainState;
				this.startTrainName = res.data.startTrainName;
				this.departureTime = res.data.departureTime;
				this.timeLength = res.data.timeLength;
				this.endTrainState = res.data.endTrainState;
				this.endTrainName = res.data.endTrainName;
				this.arrivalTime = res.data.arrivalTime;
				this.seat = res.data.seat;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onShow: function() {
		this.$fire.on('queryDate', result => {
			this.godate = result[0].newdate;
			this.dateweek = result[0].newweek;
		});
	}
}
